import React from 'react';
import Row from './components/Row';
import TopBar from './components/TopBar';
import './App.css';

class item {
  constructor(value,key){
    this.value = value;
    this.key = key;
    this.css = "";
  }
  
}

class pairOfItems {
  constructor(first,second){
    this.first = first;
    this.second = second;
  }
  
}

const clone = (items) => items.map(item => Array.isArray(item) ? clone(item) : item);
    

var testArray = []
for(var i = 0; i < 10; i++){
  testArray.push(new item(i+1,i));
  
}

//var testArray = [1,2,3,4,5,6,7,8,9];

//testArray = testArray.map(myFunction);
//console.log(testArray);

function myFunction(value,index,arr){
  return value = value;
}

class App extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      inputType: 'numbers', 
      numberOfItems: '10',
      sortAlgorithmType: 'bubble sort', 
      items: testArray, 
      index: 0
    }; //removed .keys() 
    //console.log("App Constructor");
  }

  callback_SADropdown = (SAType) => {
    this.setState({sortAlgorithmType: SAType});
  }

  callback_ITDropdown = (ITType) => {
    this.setState({inputType: ITType});
  }

  callback_NIDropdown = (NIType) => {
    this.setState({numberOfItems: NIType});
    var tempArr = Array(parseInt(NIType));
    for(var i = 0; i < parseInt(NIType); i++){
      tempArr[i] = new item(i+1,i);
    }
    this.setState({items: tempArr});
    //console.log(tempArr);
    //console.log(this.state.items);
    
  }

  swap = (outputArray, graduallySortedItems, i) => {
    var first,second, temp;
    
    //for(var i = 0; i < outputArray.length; i++) {
      first = outputArray[i].first;
      second = outputArray[i].second;
      temp = graduallySortedItems[first].value;
      graduallySortedItems[first].value = graduallySortedItems[second].value;
      graduallySortedItems[second].value = temp;
      this.setState({items: graduallySortedItems}); 
      this.setState({index: i+1});
      //console.log(i);
    //}

    
    /*
    this.swap
    setState
    iteratethrougharray
    wait
    */
  }

  bubbleSort = (e) => {
    e.preventDefault();
    var outputArray = [];
    var swapp;
    var numberOfItems = parseInt(this.state.numberOfItems) - 1;
    
    var x = JSON.parse(JSON.stringify(this.state.items));    
    //console.log(x.map((item)=> {return item.value}));
    //console.log(graduallySortedItems.map((item)=> {return item.value}));
    do {
        swapp = false;
        for (var i=0; i < numberOfItems; i++) {
            if (x[i].value < x[i+1].value) {
              //console.log(x[i].key + " " + x[i+1].key);
              outputArray.push(new pairOfItems(x[i].key, x[i+1].key));
               var temp = x[i].value;
               x[i].value = x[i+1].value;
               x[i+1].value = temp;
               swapp = true;
              }
        }
        //numberOfItems--;
    } while (swapp);
    //console.log(x.map((item)=> {return item.value}));
    //console.log(outputArray.map((item)=> `${item.first}  ${item.second}`));
    //console.log(x.map((item)=> {return item.value}));
    this.renderArray(outputArray);
    
  }

  changeItemRandomly = i => {
    this.setState(state => {
      const items = state.items.map((item, j) => {
        
        if (j === i) {          
          item.value = parseInt(Math.random()*this.state.numberOfItems + 1);
          return item;
        } else {
          return item;
        }
      });
      return {
        items,
      };
    });
    //console.log(i);
  };

  applyCSSToElements = (first,second, cssProperty) =>{
    var updatedItems = this.state.items.map((item)=> {
      if(item.key == first||item.key == second) {
        item.css = cssProperty;
        return item;
      }
      else return item;
    });
    this.setState({items: updatedItems});
  }

  addClass = (outputArray,x,classname) => {
    
    if(document.getElementById(outputArray[x].first).classList.contains(classname)){
      document.getElementById(outputArray[x].first).classList.remove(classname);
    } else document.getElementById(outputArray[x].first).classList.add(classname);
    if(document.getElementById(outputArray[x].second).classList.contains(classname)){
      document.getElementById(outputArray[x].second).classList.remove(classname);
    } else document.getElementById(outputArray[x].second).classList.add(classname);
  }

  renderArray = (outputArray) => {
    if(outputArray.length == 0) return;
    var graduallySortedItems = this.state.items;
    var x = 0;
    var cash;
    
    var intervalID = setInterval(() => {
      
      if(x == cash){
        this.addClass(outputArray,x,"pizza");
      this.swap(outputArray, graduallySortedItems, x);
      document.getElementById(outputArray[x].first).classList.remove("pizza");

      if (++x === outputArray.length) {
        window.clearInterval(intervalID);
      }
      } else {
        cash = x;
        this.addClass(outputArray,x,"pizza");
      }
        
      /*
      //document.getElementById(outputArray[x].first).classList.add("pizza");
      this.addClass(outputArray,x,"pizza");
      this.swap(outputArray, graduallySortedItems, x);
      //document.getElementById(outputArray[x].first).classList.remove("pizza");

      if (++x === outputArray.length) {
        window.clearInterval(intervalID);
      }*/
      
    }, 500);
    
    
  }

  render() {
    //console.log("App Render called");
    //console.log(this.state.items);
    return (
      <div className="App">
        <TopBar SAcallBack = {this.callback_SADropdown} NIcallBack = {this.callback_NIDropdown} ITcallBack = {this.callback_ITDropdown}/>
        
        <Row items={this.state.items} CBchangeItem={this.changeItemRandomly}/>
        <p>ctrl+click or ctrl+hover to scramble</p>
        <button onClick={ this.bubbleSort }>BubbleSort</button>
      </div>
    );
  }
    
  
}

export default App;
